
import { defineComponent, ref } from 'vue'
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue'
import { modalController } from '@ionic/vue';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";
import type { COM21400212_REQ, IBServiceDetail, RoleList } from "@/shared/type/COM21400212.type";

export default defineComponent({
  name: "com214002sc01",
  components: {
    BreadCrumb,
    Pagination,
    ModifyCard
  },
  data() {
    // Data Field 
    const serviceInfo = ref<IBServiceDetail>({
      channelCategoryCode: "",
      serviceID: "",
      serviceIDDescription: "",
      serviceIDName: "",
      transactionControlStatusCode: "01",
      firstRegisterTellerName: "",
      firstRegisterBranchCode: "",
      firstRegisterTellerID: "",
      firstRegisterDate: "",
      firstRegisterTime: "",
      lastChangeBranchCode: "",
      lastChangeTellerName: "",
      lastChangeTellerID: "",
      lastChangeDate: "",
      lastChangeTime: "",
      list: [] as RoleList[],
      checkServiceYN: "Y"
    });
    const tmpServiceInfo = {} as IBServiceDetail;
    const header = [
      { columnName: "userRoleID", sortable: true },
      { columnName: "userRoleName" },
      { columnName: "serRoleDescription" },
    ];
    const viewType = this.$route.query.type;
    const isDisableField = true;
    const modifyCard = {
      registerTellerId: '',
      registerDate: '',
      registerTime: '',
      updatedTellerId: '',
      updatedDate: '',
      updatedTime: ''
    }
    return {
      approvalRes: {} as any,
      operationMode: 'normal' as screenMode,
      isApprovalRegister: false,
      serviceInfo,
      tmpServiceInfo,
      header,
      viewType,
      isDisableField,
      isInvalid: false,
      isDisableFieldID: true,
      modifyCard,
      breadcrumbs: [
        { value: 'apm201001sc00.label.home', keyPath: '/COM200001SC00', type: 'route' },
        { value: 'com214002sc00.ib_management', keyPath: '', type: 'label' },
        { value: 'com214002sc00.ib_service', keyPath: '', type: 'label' },
        { value: 'com214002sc00.ib_service_list', keyPath: 'COM214002SC00', type: 'route' },
        { value: `${this.$route.query.type == 'register' ? 'com206002sc01.ib_service_register' : 'com206002sc01.ib_service_edit'}`, keyPath: '/COM21400SC01', type: 'route' },
      ]
    }
  },
  computed: {
     BusinessCommonStatusCode(): any {
      return this.$store.state.CommonCodeModule.commonCode.BusinessCommonStatusCode
    },
     approvalType(): string {
      return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
    },
  },
 async mounted() {
    if (this.approvalType === '') {
      if (this.$route.query.type == "register") {
        this.isDisableField = false;
        this.isDisableFieldID = false;
        this.isApprovalRegister = false;
        this.serviceInfo.transactionControlStatusCode = this.BusinessCommonStatusCode[0].key;
      } else {
        this.requestIBServiceInfoDetail();
      }
    } else {
      await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
        let authDetailParsed = JSON.parse(authDetail.body.requestContent)
        const _oBody = {
          serviceID: authDetailParsed.serviceID
        };
        const res: any = await this.$request.getRequest("COM21400212", _oBody, false) as COM21400212_REQ
        this.serviceInfo.list = [] as RoleList[]
        this.approvalRes = authDetail.body
        this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
        this.serviceInfo.list = this.$util.checkServiceIdType(authDetail.body.serviceID) !== 'register' ? res.body.list : []
        this.serviceInfo.serviceIDName = authDetailParsed.serviceIDName
        this.serviceInfo.channelCategoryCode = authDetailParsed.channelCategoryCode
        this.serviceInfo.transactionControlStatusCode = authDetailParsed.transactionControlStatusCode
        this.serviceInfo.serviceID = authDetailParsed.serviceID
        this.serviceInfo.serviceIDDescription = authDetailParsed.serviceIDDescription
        this.serviceInfo.firstRegisterDate = authDetailParsed.firstRegisterDate
        this.serviceInfo.firstRegisterTime = authDetailParsed.firstRegisterTime
        this.serviceInfo.firstRegisterTellerName = authDetailParsed.firstRegisterTellerName
        this.serviceInfo.firstRegisterBranchCode = authDetailParsed.firstRegisterBranchCode
        this.serviceInfo.firstRegisterTellerID = authDetailParsed.firstRegisterTellerID
        this.serviceInfo.lastChangeTellerName = authDetailParsed.lastChangeTellerName
        this.serviceInfo.lastChangeTellerID = authDetailParsed.lastChangeTellerID
        this.serviceInfo.lastChangeDate = authDetailParsed.lastChangeDate
        this.serviceInfo.lastChangeBranchCode = authDetailParsed.lastChangeBranchCode
        this.serviceInfo.lastChangeTime = authDetailParsed.lastChangeTime
        this.serviceInfo.checkServiceYN = authDetailParsed.checkServiceYN
      })
    }
  },
  methods: {
    handleClickAction(actionType: string) {
      switch (actionType) {
        case "edit":
          this.isDisableField = false;
          break;

        case "cancel":
          this.alertDialog("are_you_sure_you_want_to_cancel_without_saving_data", 'cancel');
          break;

        default:
          this.$router.push('/COM214002SC00')
          break;
      }
    },
    // requestIBServiceInfoDetail (COM21400212)
    async requestIBServiceInfoDetail() {
      const _oBody = {
        serviceID: this.$route.query.serviceID
      };
      const res: any = await this.$request.getRequest("COM21400212", _oBody, false);
      this.modifyCard = {
        registerTellerId: res?.body.firstRegisterTellerID,
        registerDate: res?.body.firstRegisterDate,
        registerTime: res?.body.firstRegisterTime,
        updatedTellerId: res?.body.lastChangeTellerID,
        updatedDate: res?.body.lastChangeDate,
        updatedTime: res?.body.lastChangeTime
      }
      if (res.header.result) {
        this.serviceInfo = res.body;
        this.tmpServiceInfo = JSON.parse(JSON.stringify(res.body));
        this.serviceInfo.list = res.body.list
      }
    },
    // Save Condition Edit & Register 
    handleSave() {
      if (this.serviceInfo.serviceID.trim() && 
          this.serviceInfo.serviceIDName.trim() && 
          this.serviceInfo.serviceIDDescription.trim() 
          && this.serviceInfo.checkServiceYN.trim()) {
        this.$dialog.onOpen({
          content: this.$t("message.would_you_like_to_save"),
          dialogMode: 'Question',
          callback: () => {
            modalController.dismiss()
            if (this.$route.query.type == 'register') {
              this.dataRegisterIBService();
              this.isDisableField = true;
            } else {
              this.dataUpdateIBService();
              this.isDisableField = true;
            }
          }
        })
      } else {
        this.isInvalid = true
      }
    },
    // Register (COM21400221)
    async dataRegisterIBService() {
      const _sTrcode = "COM21400221";
      const _bMock = false;
      const _oBody = {
        serviceID: this.serviceInfo.serviceID,
        serviceIDDescription: this.serviceInfo.serviceIDDescription,
        serviceIDName: this.serviceInfo.serviceIDName,
        transactionControlStatusCode: this.serviceInfo.transactionControlStatusCode,
        checkServiceYN: this.serviceInfo.checkServiceYN,
      };
      const res: any = await this.$request.getRequest(_sTrcode, _oBody, _bMock);
      if (res.header.result) {
        this.$router.push('/COM214002SC00')
        this.$dialog.onOpen({
          content: this.$t("message.register_success"),
          dialogMode: 'Success',
          callback: () => {
            modalController.dismiss()
          }
        })
      }
    },

    // Update Service (COM21400231)
    async dataUpdateIBService() {
      this.isInvalid = true
      const _sTrcode = "COM21400231";
      const _bMock = false;
      const _oBody = {
        channelCategoryCode: this.serviceInfo.channelCategoryCode,
        serviceID: this.serviceInfo.serviceID,
        serviceIDDescription: this.serviceInfo.serviceIDDescription,
        serviceIDName: this.serviceInfo.serviceIDName,
        transactionControlStatusCode: this.serviceInfo.transactionControlStatusCode,
        checkServiceYN: this.serviceInfo.checkServiceYN,
      };
      const res: any = await this.$request.getRequest(_sTrcode, _oBody, _bMock);
      if (res.header.result) {
        this.$dialog.onOpen({
          content: this.$t("message.update_success"),
          dialogMode: 'Success',
          callback: () => {
            this.requestIBServiceInfoDetail();
            modalController.dismiss()
          }
        })
      }
    },

    alertDialog(alertMessage: string, onClick?: string) {
      this.$dialog.onOpen({
        content: this.$t(`message.${alertMessage}`),
        dialogMode: 'Question',
        callback: () => {
          modalController.dismiss();
          if (onClick == 'cancel') {
            if (this.viewType == 'edit') {
              this.serviceInfo = JSON.parse(JSON.stringify(this.tmpServiceInfo));
              this.isDisableField = true;
            } else {
              this.$router.push('/COM214002SC00')
            }
          } else {
            this.$router.push('/COM214002SC00')
          }
        }
      })
    }
  }
})
